import React, { RefObject } from "react";
import { Animator, ScrollPage, batch, Fade } from "react-scroll-motion";

const MobileDictionary = (props: {
  previous: RefObject<HTMLDivElement>;
  current: RefObject<HTMLDivElement>;
  next: RefObject<HTMLDivElement>;
}) => {
  return (
    <ScrollPage page={1}>
      <div ref={props.current} className="page mobile-dictionary">
        <Animator animation={batch(Fade())}>
          <div className="project">
            <div className="project-info">
              <h3>Dictionary Application for iOS/Android</h3>
              <p>
                Dictionary application with some new features like different
                accents' pronunciation. Developed on React Native using REST
                API, React Hooks and Audio player library. See the{" "}
                <a href="https://github.com/yrshva/react-native-dictionary-app">
                  source code on GitHub
                </a>
                .
              </p>
            </div>
            <video className="dictionary-video" controls muted>
              <source
                src={require("../../../images/mobile-dictionary.mp4")}
                type="video/mp4"
              />
              Your browser does not support the video tag.
            </video>
            <div className="p-1">
              <button
                className="btn-custom btn-blue m-2"
                onClick={() =>
                  props.previous.current &&
                  props.previous.current.scrollIntoView()
                }
              >
                Previous
              </button>
              <button
                className="btn-custom btn-yellow m-2"
                onClick={() =>
                  props.next.current && props.next.current.scrollIntoView()
                }
              >
                Next
              </button>
            </div>
          </div>
        </Animator>
      </div>
    </ScrollPage>
  );
};
export default MobileDictionary;
